import * as React from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import { CardActionArea } from "@mui/material";
import img232 from "./gazou/232.jpg";

export default function SimpleAccordion() {
  return (
    <div>
      <>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>わわわわとは？</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              わわわわとは、岐阜県に住んでいる大学生兼自宅警備員の男性のことである。将来の夢はイラストレーター兼自宅警備員である。
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>依頼募集中！</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              現在イラストの依頼を募集しています！詳細は
              <a href="https://skeb.jp/@wwwwillust">skeb</a>にて！
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>SNS</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              ・<a href="https://twitter.com/wwwwillust">Twitter</a>
              <br />・
              <a href="https://www.youtube.com/channel/UCtc7499z9zu8lJSOhUIDqYg">
                YouTube
              </a>
              <br />・<a href="https://www.pixiv.net/users/31270032">pixiv</a>
              <br />・<a href="https://www.instagram.com/">Instagram</a>
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>???</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography>
              <Card sx={{ maxWidth: 345 }}>
                <CardActionArea>
                  <CardMedia
                    component="img"
                    height="140"
                    image={img232}
                    alt="green iguana"
                  />
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                      宣伝！
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      よくこのイラストを見つけましたね！そうこのイラスト、見ればわかると思いますが、完成してないんです！
                      完成次第SNSに投稿したいと思います。ということで、SNSのフォローよろしくお願いします！！！
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </Typography>
          </AccordionDetails>
        </Accordion>
      </>
    </div>
  );
}
